import React, { Suspense, lazy, useEffect, useState } from 'react';
import { withRouter, Link } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import {
  Form,
  PageSection,
  Divider
  } from '@patternfly/react-core'
import TwoFactorCheck from '../../Components/twoFactorCheck/two-factor-check';
import NonAdminUser from '../../Components/nonAdminUser/non-admin-user';
import {
  PageHeader,
  PageHeaderTitle,
} from '@redhat-cloud-services/frontend-components/PageHeader';


const targetAddress = "http://localhost:8081/auth/realms/redhat-external/apis/organization/v1/my/two-factor";


import './Authentication-Factors.scss';
import { isAdminUser } from '../../Components/utils/general-utils';


/**
 * A smart component that handles all the api calls and data needed by the dumb components.
 * Smart components are usually classes.
 *
 * https://reactjs.org/docs/components-and-props.html
 * https://medium.com/@thejasonfile/dumb-components-and-smart-components-e7b33a698d43
 */
const AuthFactors = () => {
  const dispatch = useDispatch();
  var userToken = null;
  const [currentUser, updateUser] = useState({
    adminUser: false,
    twofadisplay: <NonAdminUser/>
  })

  useEffect(() => {
    insights?.chrome?.appAction?.('authentication-factors');
    insights.chrome.auth.getToken().then(token => {
      userToken = token;
      // if the user is in fact and admin user then render TwoFactorCheck
      if(isAdminUser(userToken)){
        updateUser({
          adminUser: true,
          twofadisplay: <TwoFactorCheck tokenData={userToken}/>
        })
      }
    })
  }, []);

  const determineHeader = () => {
    
  }

  return (
    <React.Fragment>

      {currentUser.adminUser === true && 
      <div>
        <PageHeader>
          <PageHeaderTitle title="Authentication Factors" className="pf-u-mb-lg" />
          <p> Manage authentication factors for your organization. </p>
        </PageHeader>
        <Divider/>
      </div>
      }

      {currentUser.twofadisplay}

  
    </React.Fragment>
  );
  

  
};

export default withRouter(AuthFactors);
